$body-color: #181818;
$yellow-color: #ffc900;
$menu-color: #000000;
$gray-color: #eee;

.navbar {
  background-color: $menu-color;
}

.navbar-item {
  background-color: $body-color;

  // &:hover {
  //   background-color: $body-color;
  //   color: $gray-color;
  // }

  // &:active {
  //   background-color: $body-color;
  //   color: $gray-color;
  // }

  // &:visited {
  //   background-color: $body-color;
  //   color: $gray-color;
  // }
}

.navbar-menu {
  background-color: $menu-color;
  // padding: .1rem 0;
  padding: 0;
}

.navbar-link {
  color: $gray-color;
  background-color: $body-color;

  &:hover {
    background-color: $body-color;
    color: $gray-color;
  }

  &:active {
    background-color: $body-color;
    color: $gray-color;
  }

  &:visited {
    background-color: $body-color;
    color: $gray-color;
  }
}

.mobile-buttons {
  margin-left: auto;
  visibility: hidden;

  @media(max-width: 1023px){
    visibility: visible;
  }
}

.nav-buttons {
  @media(max-width: 1023px){
    // visibility: hidden;
    display: none;
  }
}

.login-button {
  background-color: $menu-color;
  border: 1px solid $yellow-color;
  color: $yellow-color;
  text-transform: uppercase;

  &:hover {
    color: $yellow-color;
  }
}

.sign-button {
  color: $menu-color;
  background-color: $yellow-color;
  border: 1px solid $yellow-color;
  text-transform: uppercase;

  &:hover {
    color: $menu-color;
  }
}

.navbar-burger {
  margin-left: 0;
}



